<template>
  <div class="shop-header-container">
    <div class="shop-header">
      <p class="header-title">
        {{shopName}}
      </p>
      <div class="contact-icon" @click="openMessenger">
        <img class="contact-icon-img" src="../../assets/facebook-messenger.png" alt="Liên Hệ"/>
      </div>
    </div>
  </div>
</template>

<script>
import {shopName} from '../../service/details'
import {firebaseDB, getShopInfoFromFirebase} from '../../service/firebaseServices'

export default {
  name: 'Header',
  methods: {
    openMessenger () {
      window.open('http://m.me/trantiendangkhoa0710', '_blank')
    }
  },
  data () {
    return {
      shopName: shopName
    }
  },
  mounted () {
    getShopInfoFromFirebase(firebaseDB).then(response => (this.shopName = response.shopName))
  }
}
</script>

<style scoped>
.contact-icon {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
}
.contact-icon-img {
  height: 35px;
  width: 40px;
}
.shop-header-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 65px;
  z-index: 10;
  background-color: white;
  border-bottom: 1px solid #f5f5f5;
}
.shop-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.header-title {
  text-align: center;
  font-size: 20px;
  color: rgba(0, 0, 0, 1);
}
</style>



