import firebase from 'firebase/compat/app'
import 'firebase/firestore'
import {collection, getDocs, getFirestore} from 'firebase/firestore/lite'

const firebaseConfig = {
  apiKey: 'AIzaSyCqtJl94CcBeKsJHDfhVPevwnrgxGQqo5w',
  authDomain: 'thuycoishop.firebaseapp.com',
  projectId: 'thuycoishop',
  storageBucket: 'thuycoishop.appspot.com',
  messagingSenderId: '145463122858',
  appId: '1:145463122858:web:deff296034c7d5f34729d3',
  measurementId: 'G-YCCCGX16Z3'
}
const app = firebase.initializeApp(firebaseConfig)
export const firebaseDB = getFirestore(app)

export const getProductsFromFirebase = async (db) => {
  const products = collection(db, 'allProducts')
  const productsSnapshot = await getDocs(products)
  return productsSnapshot.docs.map(product => product.data())
}

export const getShopInfoFromFirebase = async (db) => {
  const shopInfo = collection(db, 'shopInformation')
  const shopInfoSnapshot = await getDocs(shopInfo)
  return shopInfoSnapshot.docs.map(info => info.data())[0]
}



