<template>
  <div class="intro-container">
    <div class="intro-main-div">
      <p class="intro-text">
        {{shopDesc}}
      </p>
    </div>
  </div>
</template>

<script>
import {shopDesc} from '../../service/details'
import {firebaseDB, getShopInfoFromFirebase} from '../../service/firebaseServices'

export default {
  name: 'Introduction',
  data () {
    return {
      shopDesc: shopDesc
    }
  },
  mounted () {
    getShopInfoFromFirebase(firebaseDB).then(response => (this.shopDesc = response.shopDesc))
  }
}
</script>

<style scoped>
.intro-container {
  width: 100%;
}
.intro-main-div {
  border: 1px solid black;
  width: 100%;
  margin-top: 100px;
}
.intro-text {
  font-size: 16px;
  font-family: Dancing_Script, serif;
  margin-left: 10px;
  margin-right: 10px;
}
</style>



