<template>
  <div class="market-container">
    <div style="width: 100%">
      <h2 class="marketMenuTitle">Tất cả sản phẩm</h2>
      <div class="marketMenuDesc">Rau, thịt, hải sản,... đều có!!!</div>
    </div>
    <div class="margetMenu">
      <div :class="{
        menuHalf: true,
        menuIsChosen: leftMenuIsChosen
      }" @click="leftMenuIsChosen = true">
        <a href="#active" style="text-decoration: none; color: black;">
          Có hàng
        </a>
      </div>
      <div :class="{
        menuHalf: true,
        menuIsChosen: !leftMenuIsChosen
      }" @click="leftMenuIsChosen = false">
        <a href="#inActive" style="text-decoration: none; color: black;">
          Chưa có hàng
        </a>
      </div>
    </div>
    <div style="width: 100%; margin-top: 20px;">
      <img src="../../../assets/vegetable.jpg" alt="rau cu" style="width: 100%; height: 100%;"/>
    </div>
    <div style="width: 100%" id="active">
      <h2 class="marketMenuTitle">Sản phẩm có hàng</h2>
      <div class="marketMenuDesc">Các sản phẩm ở đây em đang có hàng ạ</div>
    </div>
    <div class="product-container">
      <Product v-for="productInfo in allProducts.filter(product => product.active)"
               :title="productInfo.product_name"
               :description="productInfo.product_description"
               :price="productInfo.product_price"
               :ship-price="productInfo.product_ship_price"
               :img-url="productInfo.imgUrl"
               :key="JSON.stringify(productInfo)"
      />
    </div>
    <div style="width: 100%; margin-top: 50px;" id="inActive">
      <h2 class="marketMenuTitle">Sản phẩm chưa có hàng</h2>
      <div class="marketMenuDesc">Các sản phẩm ở đây em chưa có hàng ạ, các chị cứ theo dõi nhé, lúc nào có em sẽ cập nhật ạ.</div>
    </div>
    <div class="product-container">
      <Product v-for="productInfo in allProducts.filter(product => !product.active)"
               :title="productInfo.product_name"
               :description="productInfo.product_description"
               :price="productInfo.product_price"
               :ship-price="productInfo.product_ship_price"
               :img-url="productInfo.imgUrl"
               :is-in-active="true"
               :key="JSON.stringify(productInfo)"
      />
    </div>
  </div>
</template>

<script>
import Product from './Product'
import {allProducts} from '../../../service/product_data'
import {getProductsFromFirebase, firebaseDB} from '../../../service/firebaseServices'

export default {
  name: 'Market',
  components: {Product},
  data () {
    return {
      leftMenuIsChosen: true,
      allProducts: allProducts
    }
  },
  mounted () {
    getProductsFromFirebase(firebaseDB).then(response => (this.allProducts = response))
  }
}
</script>

<style scoped>
.product-container {
  width: 100%;
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
}
.menuHalf {
  width: 50%;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}
.menuIsChosen {
  border-bottom: 2px solid #666666;
}
.margetMenu {
  margin-top: 35px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  font-family: Dancing_Script, sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  border-bottom: 1px solid #999999;
  height: 50px;
}
.market-container {
  margin: 40px auto 0;
  width: 100%;
  text-align: left;
}
.marketMenuDesc {
  font-family: Dancing_Script, sans-serif;
  font-size: 16px;
  position: relative;
  margin-top: -10px;
}
</style>



