<template>
  <div :class="{
    'product-container': true,
    'inactive-product-container': isInActive
  }">
    <div class="product-info">
      <div class="product-description">
        <div style="margin-bottom: 5px; width: 100%; font-family: Futura, serif; font-weight: bold; font-size: 20px;">
          {{title}}
        </div>
        <div style="width: 100%; font-family: Dancing_Script, serif;">
          {{
            description
          }}
        </div>
      </div>
      <div class="product-img-container" @click="imgIndex = (imgIndex + 1) % (imgUrl.length > 0 ? imgUrl.length : 1)">
        <img :src="imgUrl[imgIndex]" alt="minh hoa" class="product-img">
      </div>
    </div>
    <div style="width: 100%; font-family: Dancing_Script, serif;">
      <p>
        <b>Giá:</b> {{price}}
      </p>
    </div>
    <div style="width: 100%; font-family: Dancing_Script, serif;">
      <p>
        <b>Giá ship</b>: {{ shipPrice }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Product',
  props: {
    title: String,
    description: String,
    price: String,
    shipPrice: String,
    imgUrl: Array,
    isInActive: Boolean
  },
  data () {
    return {
      imgIndex: 0
    }
  }
}
</script>

<style scoped>
.inactive-product-container {
  opacity: 0.6;
}
.inactive-product-container:hover {
  opacity: 1;
}
.product-container {
  width: 100%;
  max-width: 450px;
  min-height: 120px;
  border: 1px solid #f5f5f5;
  padding: 20px;
  line-height: 1.6;
  margin-bottom: 10px;
  margin-right: 10px;
}
.product-info {
  /*height: 65%;*/
  display: flex;
}
.product-img-container {
  width: 35%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
}
.product-img {
  width: 100%;
  max-width: 180px;
  max-height: 180px;
  object-fit: cover;
  float: right;
}
.product-description {
  width: 65%;
  padding-right: 5px;
}
</style>



