<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted () {
    let currentLink = this.$route.path
    if (currentLink !== '/') {
      this.$router.push('/')
    }
  }
}
</script>

<style>
@font-face {
  font-family: "Dancing_Script";
  src: url('./assets/Dancing_Script/DancingScript-VariableFont_wght.ttf');
}
@font-face {
  font-family: "Futura";
  src: url('./assets/futura/futur.ttf');
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



